import { IValidationResult } from "./../../models/models";
import { EmailValidationRegEx } from "./regEx";

export const emailValidation = (newValue: string): IValidationResult => {
  const res: IValidationResult = {
    result: false,
    errorMsg: "",
  };

  if (newValue.trim().length === 0) {
    res.result = true;
    res.errorMsg = "    ";
    return res;
  }

  if (/,/.test(newValue)) {
    res.result = true;
    res.errorMsg = "Email      ";
    return res;
  }

  if (!/^(?=[a-zA-Z0-9])/.test(newValue)) {
    res.result = true;
    res.errorMsg =
      "Email     ,      ,  ";
    return res;
  }

  if (
    (!newValue.includes("@") && newValue.length > 25) ||
    newValue.split("@")[0].length > 25
  ) {
    res.result = true;
    res.errorMsg = "    @     25";
    return res;
  }

  if (!newValue.includes("@") && newValue.length > 25) {
    res.result = true;
    res.errorMsg =
      "    @     25 ";
    return res;
  }

  if (!/@/.test(newValue)) {
    res.result = true;
    res.errorMsg = "Email      @";
    return res;
  }

  // text lenght after @ and before (.com/.ru) can`t be > 25
  if (
    (newValue.includes("@") &&
      newValue.split("@")[1].split(".")[0].length > 25) ||
    (newValue.includes("@") && newValue.split("@")[1].split(".")[0].length < 1)
  ) {
    res.result = true;
    res.errorMsg =
      "    @     25    1";
    return res;
  }

  // Email need to include domain.
  if (
    (newValue.includes("@") &&
      newValue.split("@")[1].includes(".") &&
      (newValue.split("@")[1].split(".")[1].length < 2 ||
        newValue.split("@")[1].split(".")[1].length > 4)) ||
    (newValue.includes("@") && !newValue.split("@")[1].includes("."))
  ) {
    res.result = true;
    res.errorMsg =
      "Email     (.ru/com  .).      2    4";
    return res;
  }

  //final check, check
  if (!EmailValidationRegEx.test(newValue)) {
    res.result = true;
    res.errorMsg =
      "Email      ,    ";
    return res;
  }

  return res;
};

export const passwordValidation = (newValue: string): IValidationResult => {
  const res: IValidationResult = {
    result: false,
    errorMsg: "",
  };

  if (newValue.trim().length === 0) {
    res.result = true;
    res.errorMsg = "    ";
    return res;
  }

  if (!/.*\d/.test(newValue)) {
    res.result = true;
    res.errorMsg = "   ";
    return res;
  }

  if (!/.*[a-z]/.test(newValue)) {
    res.result = true;
    res.errorMsg = "      ";
    return res;
  }

  if (!/.*[A-Z]/.test(newValue)) {
    res.result = true;
    res.errorMsg = "      ";
    return res;
  }

  if (!/.*\W/.test(newValue)) {
    res.result = true;
    res.errorMsg = "    ( !%:?)";
    return res;
  }

  if (newValue.length < 8 || newValue.length > 24) {
    res.result = true;
    res.errorMsg = "     8    24 ";
    return res;
  }

  // \W   ([^a-zA-Z0-9_])    
  //            
  //             .
  if (/[--]/.test(newValue)) {
    res.result = true;
    res.errorMsg = "    ,   ";
    return res;
  }
  
  return res;
};
